:root{
  --Primary : #2B3A55;
  --Secondary : #0F6280;
  --Bg : #85A8B5;
  --Accent: #F2E5E5;
  --SocialIconSz : 5vh;
  --CurrentSection : 0;
  --SocialVis : 'visible';

  --grad_left: rgba(7, 90, 174, 1);
  --grad_right: rgba(6, 122, 224, 0);
}
.App{
  font-size: larger;
  color: var(--Accent);
}
.flexRow{
  display: flex;
  flex-direction: row;
}

.flexColumn{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.headerContainer{
  background: var(--Secondary);
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 3;
  box-shadow: 0 0 75px var(--Secondary);
}

.header{
  padding-left: 5vw;
  width: 95vw;
  display: flex;
  justify-content: space-between;
}
.menu{
  padding-right: 2vw;
}

.mobileMenu{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 40vh;
}
.menuButton{
  color: var(--Secondary);
  background-color: var(--Accent);
  
}

.menu a{
  padding: 1vw;
}

.a{
  text-decoration: none;
}

a:hover{
  cursor: pointer;
  text-decoration: underline;
}

.bodyPadding{
  padding: 5vh;
}
.body{
  margin-top: 10vh;
  height: 90vh;
}


.section{
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--Bg);
}

.aboutContainer{
  height: 90vh;
  background: linear-gradient(to right top , var(--grad_left), var(--grad_right))
  , url(./resources/about\ icons/me.jpg);
  
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center right;
  object-fit: scale-down;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
}


.aboutTextContainer{
  text-shadow: #000000;
  max-width: 50vw;
  max-width: 75vh;
  padding: 2vw;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  /* background-color: rgba(133, 168, 181, 50%); */
  /* background: radial-gradient(rgba(133, 168, 181, 50%), rgba(133, 168, 181, 0%)); */
  font-size: larger;
  font: bold;
}

.hideAbt{
  opacity: 0;
  transform: translateX(-100%);
  filter: blur(5px);
  transition: all 2s;
}

.showAbt{
  opacity: 1;
  transform: translateX(0);
  transition: all 2s;
  /* animation-delay: 5s; */
}

.card{
  padding: 10vh 10vw;
  background: var(--Primary);
  border-radius: 1.5rem;
}

.social {
  padding: 0.5vh;
  background: var(--Primary);
  border-radius: var(--SocialIconSz);
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  position: fixed;
  right: 1vw;
  z-index: 1;
}
.socLinks{
  padding: 4vh 0.5vh;
  top : 16vh;
}


.hide{
  opacity: 0;
  filter: blur(5px);
  transform: translateY(80%);
  transition: all 1s;
}

.show{
  opacity: 1;
  filter: blur(0);
  transform: translateY(0);
  transition: all 0.5s;
}

.singleLinkIcon:nth-child(2){
  transition-delay: 200ms;
}
.singleLinkIcon:nth-child(3){
  transition-delay: 400ms;
}
.singleLinkIcon:nth-child(4){
  transition-delay: 600ms;
}
.singleLinkIcon:nth-child(5){
  transition-delay: 800ms;
}
.singleLinkIcon:nth-child(6){
  transition-delay: 1000ms;
}
.singleLinkIcon:nth-child(7){
  transition-delay: 1200ms;
}

.socTog{
  bottom: 1vh;
  border-radius: 1vw;
}

.social img{
  height: var(--SocialIconSz);
  padding: 1.5vh;
  border-radius: 2vh;
}

.social img:hover{
  cursor: pointer;
  background-color: var(--Bg);
}


.center{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.aboutCard{
  height: 30vh;
  width: 30vh;
  padding: 2vh;
  background-color: var(--Primary);
  border-radius: 4vh;
  flex-direction: column;
}

.aboutCard img{
  height: 15vh;
}

.school{
  margin: -2vw;
  background: var(--Primary);
  border-radius: 1.5rem;
  display: flex;
  flex-direction: row;
}

.schoolCenter{
  height: 40vh;
  z-index: 2;
  box-shadow: 0 0 75px #000000;
}

.schoolSide{
  height: 35vh;
  z-index: 1;
  box-shadow: 0 0 100px #000000;
}
.schoolSiide{
  height: 25vh;
}

.school img{
  background-color: white;
  padding: 4vh 2vw;
  border-radius: 4vh;
  border-color: var(--Primary);
  border-width: 4px;
  border-style: solid;
}


.schoolDetails{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 2vw;
}

.schoolDetails a{
  color: var(--Accent);
  text-decoration: none;
}

.cpContainer{
  justify-content: space-around;
}

.cpProfile{
  width: 16vw;
  height: 6vh;
  padding: 1vh;
  border-radius: 5vh;
  background-color: var(--Primary);
  display: flex;
  flex-direction: row;
  margin: 1.5vh 0;
}

.flipCard{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.8s ease;
}

.flipCard:hover{
  transform: rotateY(180deg);
}
  
.flipFront{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
}
  
.flipBack{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
  transform: rotateY(180deg);
}
  

.cpLink{
  height: 6vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 2vw;
}

.cpProfile img{
  background-color: white;
  height: 5vh;
  padding: 0.5vh;
  border-radius: 5vh;
}
.cpProfile a{
  padding-left: 1vw;
}

.cpAchievement{
  text-align: center;
  height: 10vh;
  background-color: var(--Primary);
  padding: 1vh;
  display: flex;
  flex-direction: row;
  border-radius: 6vh;
  opacity: 0;
  box-shadow: 5px 5px 15px var(--Secondary);

  animation: animate 15s linear infinite;
  animation-delay: calc(3s * var(--delay));
}

.cpAchievement img{
  height: 8vh;
  border-radius: 6vh;
  padding: 1vh;
  background-color: white;
}

.cpAchText{
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 10vh;
  padding: 0 2vw;
}

.projectContainer{
  padding: 2vh;
  background-color: #CFB997;
}

.projectCard{
  height: 55vh;
  width: 55vh;
  max-width: 80vw;
  max-height: 80vw;
  padding: 1vh;
  border-radius: 6vh;
  background-color: var(--Primary);
}

.projectCard img{
  height: 55vh;
  width: 55vh;
  max-width: 80vw;
  max-height: 80vw;
  border-radius: 5vh;
}



@keyframes animate{
  0%{
    opacity: 0;
    transform: translateY( calc(100% - calc(100% * var(--delay))) ) scale(0.5);
  }
  5%, 20%{
    opacity: 0.4;
    transform: translateY( calc(100% - calc(100% * var(--delay))) ) scale(0.7);
  }
  25%, 40%{
    opacity: 1;
    transform: translateY( calc(0% - calc(100% * var(--delay))) ) scale(1);
  }
  45%, 60%{
    opacity: 0.4;
    transform: translateY( calc(-100% - calc(100% * var(--delay))) ) scale(0.7);
  }
  65%, 100%{
    opacity: 0;
    transform: translateY( calc(-100% - calc(100% * var(--delay))) ) scale(0.7);
  }
}